<script setup lang="ts">
import { computed, ref } from 'vue';

import SelectTokenModal from '@/components/modals/SelectTokenModal/SelectTokenModal.vue';
import useNumbers from '@/composables/useNumbers';
import { useTokens } from '@/providers/tokens.provider';
import { isSameAddress } from '@/lib/utils';
import { TokenInfo } from '@/types/TokenList';

export type TokenSelectProps = {
  modelValue: string;
  fixed?: boolean;
  weight?: number | string;
  disableInjection?: boolean;
  hideTokenLists?: boolean;
  excludedTokens?: string[];
  options?: string[];
  subsetTokens?: string[];
  ignoreBalances?: boolean;
};

/**
 * PROPS & EMITS
 */
const props = withDefaults(defineProps<TokenSelectProps>(), {
  modelValue: '',
  fixed: false,
  weight: 0,
  disableInjection: false,
  hideTokenLists: false,
  ignoreBalances: false,
  excludedTokens: () => [],
  subsetTokens: () => [],
  options: () => [],
});

const emit = defineEmits<{
  (e: 'update:modelValue', value: string): void;
}>();

/**
 * STATE
 */
const openTokenModal = ref(false);
const optionTokens = ref<Record<string, TokenInfo>>({});

/**
 * COMPOSABLEs
 */
const { getToken } = useTokens();
const { fNum } = useNumbers();

/**
 * COMPUTED
 */
const hasToken = computed(() => !!props.modelValue);

const token = computed((): TokenInfo | null => {
  if (!hasToken.value) return null;
  return getToken(props.modelValue);
});

/**
 * METHODS
 */
function toggleModal(): void {
  if (!props.fixed) openTokenModal.value = !openTokenModal.value;
}
</script>

<template>
  <div>
    <div
      v-if="hasToken && options.length === 0"
      :class="['token-select-input selected group one', { selectable: !fixed }]"
      @click="toggleModal"
    >
      <div class="w-8">
        <BalAsset :address="token?.address" class="shadow" />
      </div>
      <span class="text-base font-medium">
        {{ token?.symbol }}
      </span>
      <span v-if="Number(weight) > 0" class="ml-2 text-secondary">
        {{
          fNum(weight, {
            style: 'percent',
            maximumFractionDigits: 0,
          })
        }}
      </span>
      <BalIcon
        v-if="!fixed"
        name="chevron-down"
        size="sm"
        class="ml-2 text-blue-600 group-hover:text-purple-500 dark:text-blue-400 dark:group-hover:text-yellow-500 transition-colors"
      />
    </div>
    <BalDropdown
      v-else-if="hasToken && fixed && options.length > 0"
      :options="options"
      minWidth="40"
      @selected="emit('update:modelValue', $event)"
    >
      <template #activator>
        <div class="group token-select-input selected selectable">
          <div class="w-8">
            <BalAsset :address="token?.address" class="shadow" />
          </div>
          <span class="text-base font-medium">
            {{ token?.symbol }}
          </span>
          <span v-if="Number(weight) > 0" class="ml-2 text-secondary">
            {{
              fNum(weight, {
                style: 'percent',
                maximumFractionDigits: 0,
              })
            }}
          </span>
          <BalIcon
            name="chevron-down"
            size="sm"
            class="ml-2 text-blue-500 group-hover:text-purple-500 dark:text-blue-400 dark:group-hover:text-purple-400 transition-colors"
          />
        </div>
      </template>
      <template #option="{ option: address }">
        <div
          :set="(optionTokens[address] = getToken(address) || {})"
          class="flex justify-between items-center"
        >
          <div class="flex items-center">
            <BalAsset
              :address="optionTokens[address]?.address"
              class="shadow"
            />
            <span class="ml-1 font-medium">
              {{ optionTokens[address]?.symbol }}
            </span>
          </div>
          <BalIcon
            v-if="isSameAddress(optionTokens[address].address, modelValue)"
            name="check"
            class="ml-4 text-blue-500 dark:text-blue-400"
          />
        </div>
      </template>
    </BalDropdown>

    <div
      v-else
      class="token-select-input unselected selectable"
      @click="toggleModal"
    >
      {{ $t('selectToken') }}
      <BalIcon name="chevron-down" size="sm" class="ml-2" />
    </div>

    <teleport to="#modal">
      <SelectTokenModal
        v-if="openTokenModal"
        :excludedTokens="[...excludedTokens, modelValue]"
        :subset="subsetTokens"
        :includeEther="true"
        :disableInjection="disableInjection"
        :hideTokenLists="hideTokenLists"
        :ignoreBalances="ignoreBalances"
        @close="openTokenModal = false"
        @select="emit('update:modelValue', $event)"
      />
    </teleport>
  </div>
</template>

<style scoped>
.token-select-input {
  @apply shadow rounded-lg flex items-center h-10 px-2 whitespace-nowrap;
  @apply text-sm;

  font-variation-settings: 'wght' 700;
}

.selectable {
  @apply cursor-pointer hover:shadow-none transition-shadow;
}

.unselected {
  @apply bg-blue-500 dark:bg-blue-400 text-white;
}

.selected {
  @apply bg-gray-50 dark:bg-gray-700 text-black dark:text-white;
}


.one{
  width: 100px !important;
  height: 50px !important;
  border: none;
  border-radius: 4px;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.48);
  overflow: hidden;
  font-size: 20px;
  position: relative;
  color: white;
  outline: none;
  cursor: pointer;
  width: 100%;
  -webkit-transition: background-position .7s,box-shadow .4s;
  transition: background-position .7s,box-shadow .4s;
  background-size: 110%;
  font-family: 'Oswald', sans-serif;
  box-shadow: 0px 0px 0px 2px rgba(105, 44, 44, 0.16) inset, 0px 0px 10px 0px #278fb5;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#31b1e1), to(rgba(126, 94, 162, 0.51))), url("/tokens/WBTC.png");
  background-image: linear-gradient(to bottom, #31b1e1, rgba(126, 94, 162, 0.51)), url("/tokens/WBTC.png");
}
.one:hover{
  background-position: 0% 30%;
  box-shadow: 0px 0px 0px 2px rgba(255, 255, 255, 0.16) inset, 0px 0px 30px 0px #31b1e1;
}
.one:hover:after{
  right: -40px;
  -webkit-transition: right .4s,-webkit-transform 30s .2s linear;
  transition: right .4s,-webkit-transform 30s .2s linear;
  transition: right .4s,transform 30s .2s linear;
  transition: right .4s,transform 30s .2s linear,-webkit-transform 30s .2s linear;
  -webkit-transform: scale(1);
  transform: scale(1);
}
.one:before{
  font-family: FontAwesome;
  display: block;
  position: absolute;
}
.one:before{
  -webkit-transition: all 1s;
  transition: all 1s;
  font-size: 30px;
  left: 25px;
  top: 19px;
}
.one:after{
  -webkit-transition: right .4s, -webkit-transform .2s;
  transition: right .4s, -webkit-transform .2s;
  transition: right .4s, transform .2s;
  transition: right .4s, transform .2s, -webkit-transform .2s;
  font-size: 100px;
  opacity: .3;
  right: -120px;
  top: -17px;
}
.one:hover:before {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
/* .one:after, .one:before {
  content: ">";
} */
.one b {
  color: #2ba3c7;
  font-weight: 700;
}


</style>
